import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { HttpModule ,Http} from '@angular/http';
import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { RegisterPage } from '../pages/register/register';
import { SidePage } from '../pages/side/side';
import { MusicListPage } from '../pages/music-list/music-list';
import { PlayerPage } from '../pages/player/player';
import { NewsPage } from '../pages/news/news';
import { NewsdetailPage } from '../pages/newsdetail/newsdetail';
import { VideoListPage } from '../pages/video-list/video-list';
import { AboutmePage } from '../pages/aboutme/aboutme';
import { CollectionPage } from '../pages/collection/collection';
import { SettingPage } from '../pages/setting/setting';
import { CreaterPage } from '../pages/creater/creater';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    LoginPage,
    RegisterPage,
    SidePage,
    MusicListPage,
    PlayerPage,
    NewsPage,
    NewsdetailPage,
    VideoListPage,
    AboutmePage,
    CollectionPage,
    SettingPage,
    CreaterPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp,{
      //去掉返回键的back字样
      backButtonText: '' 
    }, 
{
  links: [
    { loadChildren: '../pages/collection/collection.module#CollectionPageModule', name: 'CollectionPage', segment: 'collection', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/side/side.module#SidePageModule', name: 'SidePage', segment: 'side', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/setting/setting.module#SettingPageModule', name: 'SettingPage', segment: 'setting', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/creater/creater.module#CreaterPageModule', name: 'CreaterPage', segment: 'creater', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/player/player.module#PlayerPageModule', name: 'PlayerPage', segment: 'player', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/music-list/music-list.module#MusicListPageModule', name: 'MusicListPage', segment: 'music-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/newsdetail/newsdetail.module#NewsdetailPageModule', name: 'NewsdetailPage', segment: 'newsdetail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/news/news.module#NewsPageModule', name: 'NewsPage', segment: 'news', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/video-list/video-list.module#VideoListPageModule', name: 'VideoListPage', segment: 'video-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/aboutme/aboutme.module#AboutmePageModule', name: 'AboutmePage', segment: 'aboutme', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    LoginPage,
    RegisterPage,
    SidePage,
    MusicListPage,
    PlayerPage,
    NewsPage,
    NewsdetailPage,
    VideoListPage,
    AboutmePage,
    CollectionPage,
    SettingPage,
    CreaterPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



